import { LAYOUT } from '@/constants'
export default ({ hiddenList = [], dataList }) => {
  let data: any = {
    id: '_id',
    props: {
      ...LAYOUT.formItemLayout
    },
    data: [
      {
        type: 'input',
        title: '',
        name: 'username',
        options: {
          initialValue: '',
          rules: [{ required: true, message: '' }]
        },
        props: {
          placeholder: ''
        }
      },
      {
        type: 'input',
        title: '',
        name: 'nickname',
        options: {
          initialValue: '',
          rules: []
        },
        props: {
          placeholder: ''
        }
      },
      {
        type: 'select',
        title: '',
        name: 'role',
        options: {
          initialValue: [],
          rules: []
        },
        props: {
          mode: 'multiple',
          placeholder: ''
        }
      },
      {
        type: 'inputPassword',
        title: '',
        name: 'password',
        options: {
          initialValue: '',
          rules: [{ required: true, message: '' }]
        },
        props: {
          placeholder: ''
        }
      }
    ]
  }
  if (hiddenList.length > 0) {
    data.data = data.data.filter((v: any) => !hiddenList.includes(v.name))
  }

  data.data = data.data.map((v: any) => {
    for (let i in dataList) {
      if (i === v.name) {
        v.dataList = dataList[i]
      }
    }
    return v
  })

  return data
}
