import { LAYOUT } from '@/constants'
export default ({ hiddenList = [], dataList = {} }) => {
  let data: any = {
    id: '_id',
    props: {
      ...LAYOUT.formItemLayout
    },
    data: [
      {
        type: 'treeSelect',
        title: '',
        name: 'parentId',

        options: {
          initialValue: '0',
          rules: [{ required: true, message: '' }]
        },
        props: {
          placeholder: '',
          treeDefaultExpandAll: true
        }
      },
      {
        type: 'input',
        title: '',
        name: 'text',
        options: {
          initialValue: '',
          rules: []
        },
        props: {
          placeholder: ''
        }
      },
      {
        type: 'input',
        title: '',
        name: 'link',
        options: {
          initialValue: '',
          rules: []
        },
        props: {
          placeholder: ''
        }
      },
      {
        type: 'input',
        title: '',
        name: 'icon',
        options: {
          initialValue: '',
          rules: []
        },
        props: {
          placeholder: ''
        }
      },
      {
        type: 'switch',
        title: '',
        name: 'status',
        options: {
          initialValue: true,
          valuePropName: 'checked',
          rules: []
        },
        props: {
          checkedChildren: '',
          unCheckedChildren: ''
        }
      },
      {
        type: 'inputNumber',
        title: '',
        name: 'sort',
        options: {
          initialValue: 0,
          rules: []
        },
        props: {}
      }
    ]
  }

  // 
  if (hiddenList.length > 0) {
    data.data = data.data.filter((v: any) => !hiddenList.includes(v.name))
  }

  // 
  data.data = data.data.map((v: any) => {
    for (let i in dataList) {
      if (i === v.name) {
        v.dataList = dataList[i]
      }
    }
    return v
  })

  return data
}
