import * as React from 'react'
import { Button, Popconfirm, Tag } from 'antd'
import * as moment from 'moment'
import { oc } from '@/utils'

export default ({ onDel, openModal, dataList }) => {
  return [
    {
      title: '',
      dataIndex: 'username',
      key: 'username'
    },
    {
      title: '',
      dataIndex: 'nickname',
      key: 'nickname'
    },
    {
      title: '',
      dataIndex: 'role',
      key: 'role',
      render: (item: any) =>
        item.map((v: any, index: number) => (
          <Tag key={index}>
            {oc(
              oc(dataList)
                .role([])
                .find((dv: any) => dv.value === v)
            ).title('')}
          </Tag>
        ))
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      render: (item: any) => moment(item).format('YYYY-MM-DD HH:mm:ss')
    },
    {
      title: '',
      dataIndex: 'lastLoginAt',
      key: 'lastLoginAt'
    },
    {
      title: '',
      render: (row: any) => {
        return (
          <>
            <Button
              onClick={() => {
                openModal(row)
              }}
              size="small"
              type="link"
            >
              
            </Button>

            <Button size="small" type="link">
              <Popconfirm
                title="?"
                onConfirm={() => {
                  onDel(row._id)
                }}
              >
                <a href="#"></a>
              </Popconfirm>
            </Button>
          </>
        )
      }
    }
  ]
}
