import * as React from 'react'
import { Button, Popconfirm } from 'antd'
import * as moment from 'moment'

export default ({ onDel, openModal }) => {
  return [
    {
      title: '',
      dataIndex: 'name',
      key: 'name'
    },
    {
      title: '',
      dataIndex: 'permissions',
      key: 'permissions'
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      render: (item: any) => moment(item).format('YYYY-MM-DD HH:mm:ss')
    },
    {
      title: '',
      dataIndex: 'updatedAt',
      key: 'updatedAt',
      render: (item: any) => moment(item).format('YYYY-MM-DD HH:mm:ss')
    },
    {
      title: '',
      render: (row: any) => {
        return (
          <>
            <Button
              onClick={() => {
                openModal(row)
              }}
              size="small"
              type="link"
            >
              
            </Button>

            <Button size="small" type="link">
              <Popconfirm
                title="?"
                onConfirm={() => {
                  onDel(row._id)
                }}
              >
                <a href="#"></a>
              </Popconfirm>
            </Button>
          </>
        )
      }
    }
  ]
}
