import * as React from 'react'
import { Form, Icon, Input, Button, Tabs, message } from 'antd'
import { fetchLogin } from '@/services/login'
import { params } from '@/utils'
import './login.css'

const { useState } = React
const Login = ({ history, form }) => {
  const [loading, setLoading] = useState(false)
  const { getFieldDecorator, validateFields } = form

  function onLogin() {
    setLoading(true)
    validateFields((err: any, values: any) => {
      if (!err) {
        const redirect = params().get('redirect')
        fetchLogin(values).then((response: any) => {
          if (response.code === 0) {
            message.success('', 1, () => {
              setLoading(false)
              history.push(redirect || '/')
            })
          } else {
            setTimeout(() => {
              setLoading(false)
            }, 2500) // request.js
          }
        })
      }
    })
  }

  return (
    <div className="login-container">
      <div className="login-box">
        <Icon type="cloud" className="login-logo" />
        <Form>
          <Tabs defaultActiveKey="1">
            <Tabs.TabPane tab={<span></span>} key="1">
              <Form.Item>
                {getFieldDecorator('username', {
                  initialValue: ''
                })(
                  <Input
                    size="large"
                    prefix={
                      <Icon
                        type="mobile"
                        style={{ color: 'rgba(0,0,0,.25)' }}
                      />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>
              <Form.Item>
                {getFieldDecorator('password', {
                  initialValue: ''
                })(
                  <Input.Password
                    size="large"
                    prefix={
                      <Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />
                    }
                    placeholder=""
                  />
                )}
              </Form.Item>
            </Tabs.TabPane>
            <Tabs.TabPane tab={<span></span>} key="2">
              <Form.Item>
                <Input
                  size="large"
                  prefix={
                    <Icon type="mobile" style={{ color: 'rgba(0,0,0,.25)' }} />
                  }
                  placeholder=""
                />
              </Form.Item>
              <Form.Item>
                <div style={{ display: 'flex' }}>
                  <Input
                    size="large"
                    prefix={
                      <Icon type="mail" style={{ color: 'rgba(0,0,0,.25)' }} />
                    }
                    placeholder=""
                  />
                  <Button size="large" style={{ marginLeft: '10px' }}>
                    
                  </Button>
                </div>
              </Form.Item>
            </Tabs.TabPane>
          </Tabs>
          <Form.Item>
            <div>
              <Button
                loading={loading}
                type="primary"
                size="large"
                block
                onClick={onLogin}
              >
                 
              </Button>
            </div>
          </Form.Item>
        </Form>
      </div>
    </div>
  )
}

export default Form.create()(Login)
