import * as React from 'react'
import { Button, Popconfirm, Switch } from 'antd'
import * as moment from 'moment'
import { Input, Icon } from 'antd'

export default ({ onDel, openModal, onSave, handleChangeSort }) => {
  return [
    {
      title: '',
      dataIndex: 'text',
      key: 'text'
    },
    {
      title: '',
      dataIndex: 'link',
      key: 'link'
    },
    {
      title: '',
      dataIndex: 'icon',
      key: 'icon',
      render: (item: any) => {
        return item && <Icon type={item} />
      }
    },
    {
      title: '',
      dataIndex: 'status',
      key: 'status',
      render: (item: any, row: any) => (
        <Switch
          checkedChildren=""
          unCheckedChildren=""
          defaultChecked={!!item}
          onChange={checked => {
            onSave({
              _id: row._id,
              status: +checked
            })
          }}
        />
      )
    },
    {
      title: '',
      dataIndex: 'createdAt',
      key: 'createdAt',
      render: (item: any) => moment(item).format('YYYY-MM-DD HH:mm:ss')
    },
    {
      title: '',
      dataIndex: 'updatedAt',
      key: 'updatedAt',
      render: (item: any) => moment(item).format('YYYY-MM-DD HH:mm:ss')
    },
    {
      title: '',
      dataIndex: 'sort',
      key: 'sort',
      render: (item: any, row: any) => {
        return (
          <Input
            style={{ width: 60 }}
            value={item || 0}
            onChange={e => {
              handleChangeSort(e.target.value, row._id)
            }}
            onBlur={e => {
              handleChangeSort(e.target.value, row._id, true)
            }}
            onPressEnter={(e: any) => {
              handleChangeSort(e.target.value, row._id, true)
            }}
          />
        )
      }
    },
    {
      title: '',
      render: (row: any) => {
        return (
          <>
            <Button
              onClick={() => {
                openModal(row)
              }}
              size="small"
              type="link"
            >
              
            </Button>

            <Button size="small" type="link">
              <Popconfirm
                title="?"
                onConfirm={() => {
                  onDel(row._id)
                }}
              >
                <a href="#"></a>
              </Popconfirm>
            </Button>
          </>
        )
      }
    }
  ]
}
