import { clone } from '@/utils'
/**
 * 
 * #### Example
 * ```typescript
 * import { filterObjectProps } from "@/utils"
 * const data = { id: 1, age: 21, name: 'lisi'}
 * const filterData = filterObjectProps(data, ['age', 'name']) // { age: 21, name: 'lisi' }
 * ```
 * @category Utils
 */
export const filterObjectProps = (obj = {}, props = []) => {
  const data = {}
  obj = clone(obj) //  copy 
  props.forEach(v => {
    data[v] = obj[v]
  })
  return data
}
